.main__portfolio .main__bar a {
  margin: 0px 10px 0px 0px;
  color: grey;
  border: none;
}

.main__portfolio .main__bar:nth-child(1) {
  margin-left: 0px;
}

.main__portfolio .main__bar .is-used {
  background-color: #ed4a09;
  color: white;
  padding: 8px 13px;
}

.portfolio {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(230px, 1fr));
  grid-gap: 32px;
  grid-auto-flow: dense;
}

@media screen and (max-width: 1210px) {
  .portfolio {
    margin: 20px 100px;
  }
}

@media screen and (max-width: 940px) {
  .portfolio {
    margin: 20px 50px;
  }
}

.portfolio__work {
  width: 73%;
  height: 300px;
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 1100px) {
  .portfolio__work {
    align-items: center;
  }
}

@media screen and (max-width: 420px) {
  .portfolio__work {
    align-items: normal;
  }
}

.portfolio__image {
  display: inline-block;
  position: relative;
  line-height: 0;
}

.portfolio__image:hover:after {
  content: "";
  display: block;
  width: 264px;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: transparent;
  background-color: black;
  opacity: 0.6;
}

@media screen and (max-width: 420px) {
  .portfolio__image:hover:after {
    width: 210px;
  }
}

.portfolio__image:hover:before {
  content: "";
  display: inline-block;
  width: 52px;
  height: 52px;
  margin: -26px 0 0 -26px;
  position: absolute;
  top: 50%;
  left: 132.5px;
  background: url(https://snipp.ru/img/plus.png) 0 0 no-repeat;
  z-index: 100;
}

@media screen and (max-width: 420px) {
  .portfolio__image:hover:before {
    left: 60px;
  }
}

.portfolio__image img {
  width: 265px;
  height: 220px;
  object-fit: cover;
}

@media screen and (max-width: 420px) {
  .portfolio__image {
    width: 210px;
    height: 190px;
  }
}

.portfolio__text {
  border: 2px solid #e6e2e1;
  border-top: none;
  padding: 15px;
  width: 265px;
  position: relative;
  text-transform: uppercase;
  font-weight: bold;
}

@media screen and (max-width: 420px) {
  .portfolio__text {
    width: 210px;
  }
}

.portfolio__text p {
  margin-top: 0px;
  margin-bottom: 0px;
  color: grey;
  font-size: 21px;
}

.portfolio__text span {
  color: #ed4a09;
  font-size: 12px;
}

.portfolioItem {
  width: 69%;
  margin: 25px 15%;
}

@media screen and (max-width: 800px) {
  .portfolioItem {
    width: 99%;
    margin: 25px 10px;
  }
}

.portfolioItem__content {
  display: flex;
  flex-direction: column;
  margin-bottom: 50px;
}

@media screen and (max-width: 500px) {
  .portfolioItem__content {
    font-size: 12px;
  }
}

.portfolioItem__content img {
  object-fit: cover;
  height: 500px;
  width: 100%;
  margin-top: 10px;
}

@media screen and (max-width: 800px) {
  .portfolioItem__content img {
    height: 400px;
  }
}

@media screen and (max-width: 500px) {
  .portfolioItem__content img {
    height: 300px;
  }
}

@media screen and (max-width: 500px) {
  .portfolioItem__content img {
    height: 200px;
  }
}

.portfolioItem__content p {
  margin: 15px 0px;
}

.portfolioItem__content p:nth-child(1) {
  margin-top: 10px;
}

.portfolioItem__content p:nth-child(2) {
  margin-bottom: 20px;
}

body {
  background-color: white;
  font-family: Arial Narrow, sans-serif;
  font-stretch: condensed;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  width: 100%;
}

body .main__block__main {
  width: 100%;
  height: 100%;
}

body .main__block__main .title__main {
  display: flex;
  align-items: center;
  width: 100%;
  align-items: flex-start;
}

body .main__block__main .title__main__text {
  border-right: 10px solid white;
  border-left: 10px solid white;
  display: flex;
  padding: 0 10px;
  align-items: center;
  height: 75px;
  width: 100%;
  color: white;
  font-size: 36px;
  font-weight: bold;
  background-color: #ed4a09;
}

@media screen and (max-width: 1100px) {
  body .main__block__main .title__main__text {
    font-size: 30px;
    height: 50px;
  }
}

@media screen and (max-width: 820px) {
  body .main__block__main .title__main__text {
    font-size: 36px;
    height: 75px;
    border-right: 0px;
    border-left: 0px;
    margin-left: 10px;
  }
}

@media screen and (max-width: 720px) {
  body .main__block__main .title__main__text {
    font-size: 30px;
    height: 50px;
  }
}

@media screen and (max-width: 550px) {
  body .main__block__main .title__main__text {
    font-size: 20px;
    height: 40px;
  }
}

body .main__block__main .title__main::after, body .main__block__main .title__main::before {
  content: "";
  background-color: #e88964;
  height: 75px;
  display: flex;
  align-items: flex-start;
}

@media screen and (max-width: 1100px) {
  body .main__block__main .title__main::after, body .main__block__main .title__main::before {
    height: 50px;
  }
}

@media screen and (max-width: 820px) {
  body .main__block__main .title__main::after, body .main__block__main .title__main::before {
    display: none;
  }
}

body .main__block__main .title__main::after {
  width: 22%;
}

body .main__block__main .title__main::before {
  width: 20%;
}
